
.yd-navbar:after{
	border:0;
}
.my-rank-wrap{
	width:100%;
	height:4.5rem;
	background:#f46094;
	position: relative;
}
.my-rank-header{
	width:80%;
	margin:0 auto;
	color:#fff;
	font-size:.3rem;
}
.user-name{
	font-size:.5rem;
	font-weight:600;
}
.my-rank-step{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	margin:.6rem 0;
}
.user-img{
	width:1rem;
	border-radius: 50%;
}
.step{
	width:80%;
	height:.1rem;
	background:#fff;
	position: relative;
	top:.45rem;
	color:#fff;
	font-size:.2rem;
}
.step b{
	font-size:.4rem;
}
.step li{
	width:.5rem;
	height:.5rem;
	background:#f46094;
	border:.1rem solid #fff;
	position: absolute;
	top:-0.18rem;
	border-radius: 50%;
}
.step span{
	position: absolute;
	left:.5rem;
	top:-.4rem;
	width:2rem;
}
.step .bottom{
	top:.2rem;
}
.step li:nth-child(1){
	left:10%;
}
.step li:nth-child(2){
	left:58%;
}
.my-rank-body{
	width:90%;
	margin:0 auto;
}
.list{
	color:rgb(181,182,197);
}
.icon-img{
	width:.6rem;
}
.yd-list-theme4{
	border-radius:.2rem;
}
.yd-list-theme4 .yd-list-item .yd-list-img{
	width: 1rem;
    padding: 25px 0;
}
.yd-list-img img{
	border-radius: 50%;
}
.yd-list-theme4 .yd-list-item .yd-list-other{
	padding-top:0;
}
.yd-list-other>div{
	height: .8rem;
}



